@media print {
  /* Avoid cutting large blocks like headers, images, or paragraphs */
  h1,
  h2,
  h3,
  h4,
  h5,
  p,
  img,
  .section {
    page-break-inside: avoid; /* Keep these elements from splitting */
  }

  /* Force page breaks before key sections to avoid cutting between pages */
  .new-page {
    page-break-before: always;
  }

  /* Optional: Hide certain elements in the PDF, like the Download button */
  .download-button {
    display: none;
  }
}

.App {
  text-align: center;
}

h1,
h2,
h3,
h4,
h5 {
  margin-block-start: 10px;
  margin-block-end: 10px;
}

.separator {
  margin: 0px 8px;
}

.divider {
  height: 2px;
  background-color: gray;
  width: 100%;
}

header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border: solid black 1px;
  margin: 10px;
  padding: 10px 20px;
}

.header-info {
  display: flex;
  flex-direction: column;
  align-items: start;
}

.header-info-contact-details {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
}

.header-pic {
  display: flex;
  overflow: hidden;
  width: 100%;
  max-width: 150px;
  border-radius: 10%;
  border: solid black 2px;
}

.header-pic img {
  width: 100%;
  height: auto;
  border-radius: 10%;
}

.mainBody {
  display: flex;
  flex-direction: column;
  border: solid black 1px;
  margin: 10px;
  padding: 10px 20px;
}

.body-experience,
.body-skill,
.body-certification,
.body-education,
.body-language,
.body-passion {
  display: flex;
  flex-direction: column;
  align-items: start;
}

.body-experience-company-jobTitle-duration {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.body-experience-team-tech,
.body-technicalSkill-li,
.body-certification-li,
.body-languages-li,
.body-passion-li {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.body-education-li {
  display: flex;
  flex-direction: column;
  align-items: start;
}

.body-experience ul {
  display: flex;
  flex-direction: column;
  align-items: start;
  text-align: start;
  width: 100%;
}

.body-experience ul li {
  width: 90%;
}

.body-experience ul li ul li {
  width: 90%;
}

.body-experience-team-tech-team,
.body-experience-team-tech-link,
.body-experience-team-tech-tech,
.body-technicalSkill-li-skill,
.body-certification-li-details,
.body-certification-li-college,
.body-education-li-college {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: solid black 1px;
  margin: 10px;
  padding: 10px 40px;
}
